.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}
.sr-only-focusable:active,
.sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}

#page2 {
  position: relative;
  background: #09dca1;
}
#page2 .headerNav {
  width: 100%;
  z-index: 2;
}
#page2 .pose-absolute {
  position: absolute;
  top: 0;
}
#page2 .pose-fixed {
  position: fixed;
  top: 0;
  left: 0;
}
@media (min-width: 767px) {
#page2 .avatar {
    margin: 0 30px 0 0;
    padding-top: 120px;
}
#page2 .avatar .link-list {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
#page2 .avatar .link-list .link-item {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    display: inline-block;
    text-align: center;
    line-height: 200px;
}
#page2 .avatar .link-list .link-item:nth-child(2) {
    -webkit-box-flex: 0;
        -ms-flex: 0 0 200px;
            flex: 0 0 200px;
    height: 200px;
}
#page2 .avatar .link-list .link-item:nth-child(2) img {
    width: 200px;
    height: 200px;
    border-radius: 50%;
}
#page2 .avatar .link-list .fa {
    width: 80px;
    height: 80px;
    line-height: 80px;
    text-align: center;
    border-radius: 50%;
    background-color: #222222;
    color: #fff;
    font-size: 40px;
}
#page2 .avatar .link-list .fa:hover {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
    -webkit-transition: all 1s ease-in-out;
    transition: all 1s ease-in-out;
}
#page2 .content {
    width: 900px;
    margin: 0 auto;
    font-size: 18px;
    line-height: 34px;
}
#page2 .content::after {
    display: block;
    content: '';
    height: 0;
    overflow: hidden;
    clear: both;
}
#page2 .content .user-list {
    float: left;
    width: 300px;
    color: #fff;
}
#page2 .content .user-list .user-item i.fa {
    display: inline-block;
    width: 40px;
    text-align: center;
}
#page2 .content .user-list .user-item span,
  #page2 .content .user-list .user-item a {
    color: #fff;
    font-weight: bold;
}
#page2 .content .commit {
    float: left;
    width: 600px;
}
#page2 .content .commit p {
    text-indent: 32px;
    margin-top: 40px;
}
#page2 .content .commit p:hover {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    -webkit-transition: all 0.7s ease-in-out;
    transition: all 0.7s ease-in-out;
}
}
@media (max-width: 767px) {
#page2 .title-wrapper {
    top: 4rem;
}
#page2 .avatar {
    padding-top: 60px;
}
#page2 .avatar .link-list {
    width: 90%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    margin: 40px auto 0;
    text-align: center;
}
#page2 .avatar .link-list .link-item {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    display: inline-block;
    text-align: center;
